          "lethality": "Lethal",
          "otherNamesMentioned": "Arrested on suspicion of lynching:\nJohn Nidever\nEvenezer Nidever (son)\nCharles Callis (discharged)\nMr. McKeon\nMr. Coates\nShot by Badillo's other son:\nGeorge Nidever (another son of John Nidever)",
