          "victimRaces": null,
          "victimNotes": null,
          "oldSources": "HHB PT, I: 482",
          "oldNotes": "\"On Tuesday night, 23d August, some persons went to the house of Senor Badillo,… and after seizing upon the father, and his son aged about seventeen years, took them to a tree about twenty yards from the house, and hung them.\" A body of 50 or 60 \"native Californian,\" upon learning the news the next morning, set out to find the lynchers. \"Suspicion fastened upon an American named Nidiver, and his two sons, who were immediately captured.\" George, one of the sons, was shot, while the father and other son were imprisoned. This \"created a great excitement among the American population of S.B., and they immediately turned out in numbers to guard the jail, as it was feared that the remaining Nidivers would be taken out during the night and hung or shot.\"\n\n9-9 gives more details about nature of accusations against Badillo and son, as well as about killing of George Nidever and trial of his brother and father (John and Ebenezer), and trial of those who killed George. This is a complicated case and involves both hispanic Californian and Anglo American suspects and victims in two retaliatory incidents of lynching.\n\n9-19 adds: John and Ebenezer Nidever, --- McKeon and --- Coates, were liberated by the Grand Jury, \"having reported 'no bill.'\" Jury were split about accuseds' involvement in lynching of Badillo. \"The Mayor of the town had resigned his office... The Gazette urges that a 'Council of Safety' be formed. The feeling between the Americans and native Californians continues very bitter.\"",
