          "oldSources": "1859-08-31 San Francisco Bulletin p3c5",
          "oldNotes": "Two Indians robbed and murdered Israel Jones (\"about 40 years of age, from Warsaw, Poland\") on 25 August at Two Mile Bar. Miners caught the suspects \"and had their arms tied, with the intention of hanging them immediately.\" The Indians got away, with \"a number\" in pursuit. That mob stopped pursuing after a member of the mob was shot in friendly fire. Indians caught in Knight's Ferry with cooperation of \"a son of the Chief of the tribe\" the Indians were from. Taken to Six-Mile Bar, tried and convicted. Were awaiting \"conveyance to Mokelumne Hill, with a view to deliver them over to the Sheriff of Calaveras county.\" But \"a crowd of men\" rushed the house where they were held \"and forcibly took them to a tree a short distance above the town, and hung them until they were dead, after which the bodies were removed by the Indians of their tribe.\" Article notes \"brutal manner\" of crowd in attendance of lynching. Bodies mutilated.",
