          "oldNotes": "12-19: On 13th December, teamster stopped on road \"above Martin House… by a highwayman, and robbed of $262.50.\" Teamster told people \"in the vicinity, and the Vigilance Committee, which has been organized in that neighborhood for some time past, pursued the robber and captured him. The teamster swore to his identity.\" Man hung \"at or near Bangor[?]\" by Committee.\n\n12-21 gives more/conflicting details. Robbery said to occur Thursday night, 15th Dec., traveling near the Oregon House. Accosted by \"a man who had a piece of calico over his face, and robbed of two dollars and a half, all he had. Afterwards, the Vig. Com... arrested a man who had a similar, if not the same piece of calico in his possession; also two dollars and a half and a revolver. He was brought into the presence of the teamster, who failed to identify him, although he recognized the calico, and stated that the man was of about the same size with the one who robbed him.\" Suspect taken into woods by Vig. Com. and hasn't been seen since. \"We should judge that this was rather slender evidence upon which to dispose of a man in the way in which it is conjectured that this one has been.\"",
