          "oldSources": "Case ADD 1886-05-05 pc1 SDU Vugo Lucas Seper Antone Barovich Drytown Amador Co CHECK.pdf\nCase ADD 1886-05-05 SDU p4c2 Seper killed Vugo Amador Drytown Barovich CHECK.pdf\nCase ADD 1886-05-05 SFEB p1c8 Vugo Sepeer Jackson Amador\nCase ADD 1886-05-06 AC Seper Vugo Jackson Amador\nCase ADD 1886-05-07 p2c4 Sta Cruz Sentinel George Vugas Barovich Seper Drytown CHECK.pdf\nCase ADD 1886-05-07 San Jose Daily News p3c2\nCase ADD 1886-05-26 CHECK PAPER p3c2 Vugo Jackson Amador Co CHECK.pdf\n1886-05-05 San Francisco Bulletin p1c8\n1886-05-06 San Francisco Bulletin p1c9",
          "oldNotes": "George Vuga and Antone Barovich are Austrian immigrant prospectors living in the Black Hills, two miles from Drytown. Barovich owns a cabin and Vuga was staying at it. Barovich invites Seper to join them, which Vuga takes to mean \"his being ousted from the premises.\" \"There was no quarrel... but Vuga brooded over the situation and... he took down his gun, loaded one barrel with buckshot and the other with rifle balls. He stepped outside the cabin and saw Seper sitting on a rock and said, \"You've come to drive me out,\" took deliberate aim and fired the charge of buckshot,\" killing Seper. Vuga then went to find Barovich, shot him with the other barrel, striking him in the leg and fracturing the bone. \"The murderer is about sixty years old and is cross and desperate under slight provocation.\" Later, Vuga is examined in Drytown. At midnight after exam, \"a number of men took him from Constable Jennings... and carried him off. Vuga was afterward found hanging to a tree.\"",
